const { each, final, deepEqual } = require('ntils');
const { Entity, Node } = require('common');
const expression = require('./expression');
const { meta } = require('../decorators');

//
const types = {
  ATTRIBUTE: 'A',
  ELEMENT: 'E'
};

//
const levels = {
  PREVENT: 3000,      //prevent
  STATEMENT: 2000,    //statement
  ELEMENT: 1000,      //element
  GENERAL: 0,         //general
  ATTRIBUTE: -1000,   //any attribute
  CLOAK: -2000        //cloak
};

/**
 * 
 */
@meta({
  type: types.ATTRIBUTE,
  level: levels.GENERAL
})
class Directive extends Entity {

  static types = types;
  static levels = levels;

  //
  parseExpr = expression;
  Node = Node;

  //
  constructor(options) {
    super();
    each(options, (name, value) => final(this, name, value));
  }

  //
  bind() { }

  //
  execute(scope, force) {
    this.scope = scope;
    if (this.meta.type === types.ELEMENT) {
      return this.update();
    }
    let newValue = this.meta.literal ?
      this.attribute.value : this.expression(scope);
    if (force || !deepEqual(this._value_, newValue)) {
      this.update(newValue, this._value_);
      this._value_ = newValue;
    }
  }

  //
  update() { }

  //node  unbind
  //unbind 
  unbind() { }

}
module.exports = Directive;


